@import "https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap";
:root {
  --content-width: 1312px;
  --container-offset: 16px;
  --container-width: calc(var(--content-width)  + (var(--container-offset) * 2));
  --font-family-primary: "Montserrat", sans-serif;
  --fw-400: 400;
  --fw-500: 500;
  --fw-600: 600;
  --color-white: #fff;
  --color-black: #111;
  --color-dark: #1e1e1e;
  --color-overlay-main: #0000004d;
  --color-yellow-300: #fffbf2;
  --color-red-400: #d03030;
  --color-grey-300: #b5b5b5;
  --color-grey-400: #8a8a8a;
  --color-grey-500: #4a4a4a;
  --radius-main: 2px;
  --shadow-primary: 0px 4px 4px 0px #00000040 inset;
}

.checkbox__field:checked + .checkbox__content:after {
  opacity: 1;
}

.checkbox__field:focus + .checkbox__content:before {
  outline: 1px solid var(--color-gray-100);
  outline-offset: 1px;
}

.checkbox__field:disabled + .checkbox__content {
  opacity: .4;
  pointer-events: none;
}

html, body {
  scroll-behavior: smooth;
  overscroll-behavior-y: none;
}

.page {
  height: var(--vh);
  font-family: var(--font-family-primary);
  font-weight: var(--fw-500);
  letter-spacing: 0;
  color: var(--color-dark);
  background-color: var(--color-white);
  font-optical-sizing: auto;
  font-size: clamp(18px, 2vw, 24px);
  font-style: normal;
  line-height: 1.4;
}

.page__body {
  min-width: 320px;
  min-height: var(--vh);
}

.page.open {
  overflow: hidden;
}

section {
  padding: clamp(30px, 4.5vw, 64px) 0;
}

.container {
  padding: 0 var(--container-offset);
  width: 100%;
  max-width: var(--container-width);
  margin: 0 auto;
}

.site-container {
  min-height: var(--vh);
  grid-template-rows: 1fr auto;
  grid-template-columns: 100%;
  display: grid;
}

.main {
  padding-top: var(--header-height);
}

.overlay {
  position: relative;
}

.overlay:before {
  content: "";
  z-index: 1;
  background: var(--color-overlay-main);
  position: absolute;
  inset: 0;
}

.hidden {
  display: none !important;
}

.sr-only {
  -webkit-clip-path: inset(50%) !important;
  clip-path: inset(50%) !important;
  width: 1px !important;
  height: 1px !important;
  white-space: nowrap !important;
  clip: rect(1px, 1px, 1px, 1px) !important;
  border: 0 !important;
  margin: -1px !important;
  padding: 0 !important;
  position: absolute !important;
  overflow: hidden !important;
}

.dis-scroll {
  width: 100%;
  height: 100dvh;
  height: calc(var(--vh, 1vh) * 100);
  overscroll-behavior: none;
  position: fixed;
  top: 0;
  left: 0;
  overflow: hidden;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

h1, h2, h3 {
  overflow: hidden;
}

h1, .title, h2, .name {
  line-height: 1.25;
  font-weight: var(--fw-500);
}

h1, .title {
  font-size: clamp(28px, 4vw, 56px);
}

h2, .name {
  text-transform: capitalize;
  font-size: clamp(24px, 4vw, 48px);
}

h3 {
  font-size: clamp(22px, 3vw, 32px);
  line-height: 1.25;
  font-weight: var(--fw-600);
  text-transform: capitalize;
}

.link {
  font-size: clamp(18px, 2vw, 24px);
  line-height: 1.21;
  font-weight: var(--fw-500);
  padding: 12px 16px;
  text-decoration: underline;
  transition: color .4s ease-in-out;
}

.link:hover {
  color: var(--color-red-400);
}

.more {
  width: -moz-fit-content;
  width: fit-content;
  font-size: clamp(16px, 2vw, 20px);
  line-height: 1.2;
  font-weight: var(--fw-600);
  color: var(--color-red-400);
  padding-inline: 0 42px;
  transition: scale .4s ease-in-out;
  display: block;
  position: relative;
}

.more:hover {
  scale: 1.2;
}

.more:before {
  content: "";
  width: 28px;
  height: 28px;
  background-image: url("../images/icons/more-icon.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
}

.more--reverse {
  padding-inline: 42px 0;
}

.more--reverse:before {
  left: 0;
  right: unset;
  transform: translateY(-50%)rotate(180deg);
}

.text {
  font-size: clamp(16px, 2vw, 24px);
  font-weight: var(--fw-400);
  line-height: 1.5;
}

.accent {
  color: var(--color-grey-500);
}

input, textarea {
  font-size: 18px;
  line-height: 1.22;
  font-weight: var(--fw-500);
  color: var(--color-red-400);
  text-transform: uppercase;
}

input::placeholder, textarea::placeholder {
  color: var(--color-dark);
}

.copyright {
  font-size: clamp(18px, 2vw, 20px);
  line-height: 1.5;
  font-weight: var(--fw-400);
}

.header {
  z-index: 10;
  width: 100%;
  background-color: var(--color-yellow-300);
  border-top: 1px solid var(--color-grey-300);
  border-bottom: 1px solid var(--color-grey-300);
  padding: clamp(12px, 1.8vw, 24px) 0;
  transition-property: transform, padding;
  transition-duration: .15s;
  position: fixed;
  top: 0;
}

.header.hide {
  transform: translateY(-100%);
}

.nav {
  justify-content: space-between;
  align-items: center;
  display: flex;
  position: relative;
}

.nav img {
  height: auto;
}

.nav .logo {
  max-width: 247px;
  width: 100%;
  margin-right: 32px;
}

@media only screen and (max-width: 992px) {
  .nav .logo {
    max-width: 130px;
  }
}

.nav__inner {
  max-width: 902px;
  width: 100%;
  justify-content: space-between;
  display: flex;
}

@media only screen and (max-width: 1180px) {
  .nav__inner {
    left: 0;
    top: calc(var(--header-height) * 1);
    z-index: -1;
    max-width: 100%;
    height: calc(var(--vh));
    background-color: var(--color-yellow-300);
    flex-direction: column;
    justify-content: flex-start;
    padding: 30px 16px;
    transition-property: transform;
    transition-duration: .4s;
    position: fixed;
    overflow-y: auto;
    transform: translateX(150%);
  }
}

.nav__list {
  align-items: center;
  margin: 0 32px 0 0;
  display: flex;
}

@media only screen and (max-width: 1180px) {
  .nav__list {
    z-index: -1;
    flex-direction: column;
    margin-bottom: 30px;
    margin-right: 0;
  }
}

.nav__item:not(:last-of-type) {
  margin-right: 30px;
}

@media only screen and (max-width: 1180px) {
  .nav__item:not(:last-of-type) {
    margin-bottom: 30px;
    margin-right: 0;
  }
}

.nav__link {
  padding-block: 10px;
  font-size: 18px;
  line-height: 1.5;
  transition: color .4s ease-in-out;
}

.nav__link.true, .nav__link:hover {
  color: var(--color-red-400);
}

.nav__btns {
  align-items: center;
  gap: 20px;
  display: flex;
}

@media only screen and (max-width: 1180px) {
  .nav__btns {
    flex-direction: column;
    gap: 30px;
  }
}

[class].btn {
  width: -moz-fit-content;
  width: fit-content;
  border: 1px solid var(--color-dark);
  font-family: var(--font-family-primary);
  font-weight: var(--fw-400);
  text-align: center;
  border-radius: var(--radius-main);
  text-transform: uppercase;
  white-space: nowrap;
  background-color: #0000;
  margin: 0;
  padding: 12px 16px;
  font-size: clamp(16px, 3vw, 20px);
  line-height: 1.22;
  transition-property: background-color, color, border-color;
  transition-duration: .4s;
  transition-timing-function: ease-in-out;
  display: block;
  color: var(--color-dark) !important;
}

[class].btn--reverse, [class].btn:hover {
  background-color: var(--color-red-400);
  border-color: var(--color-red-400);
  color: var(--color-white) !important;
}

[class].btn--reverse:hover {
  border-color: var(--color-dark);
  background-color: #0000;
  color: var(--color-dark) !important;
}

[class].btn--large {
  font-size: clamp(20px, 3vw, 24px);
}

[class].swiper .swiper-button {
  width: 50px;
  height: 50px;
  border-radius: var(--radius-round);
  top: unset;
  background-image: url("../images/icons/next-icon.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  bottom: 110px;
}

[class].swiper .swiper-button:after {
  display: none;
}

[class].swiper .swiper-button--bottomed {
  bottom: 70px;
}

[class].swiper .swiper-button--centered {
  top: 50%;
  bottom: unset;
  filter: brightness(0) saturate() invert() sepia() saturate(2%) hue-rotate(340deg) brightness(106%) contrast(101%);
}

[class].swiper .swiper-button-prev {
  left: 76px;
  transform: rotate(180deg);
}

[class].swiper .swiper-button-next {
  right: 76px;
}

[class].swiper .swiper__nav {
  max-width: 220px;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  display: flex;
  position: absolute;
  bottom: 0;
  right: 336px;
}

[class].swiper .swiper__nav .swiper-button {
  top: unset;
  bottom: unset;
  left: unset;
  right: unset;
  margin: 0;
  position: relative;
}

[class].swiper .swiper__nav .swiper-pagination {
  width: -moz-fit-content;
  width: fit-content;
  white-space: nowrap;
  position: static;
}

[class].swiper .swiper__nav .swiper-pagination span {
  font-weight: var(--fw-400);
}

@media only screen and (max-width: 1280px) {
  [class].swiper .swiper__nav {
    margin-inline: auto;
    position: static;
  }
}

@media only screen and (max-width: 1180px) {
  [class].swiper .swiper-button {
    bottom: 50%;
    transform: translateY(-50%);
  }

  [class].swiper .swiper-button-prev {
    left: 16px;
    transform: rotate(180deg);
  }

  [class].swiper .swiper-button-next {
    right: 16px;
    transform: translateY(0);
  }
}

@media only screen and (max-width: 768px) {
  [class].swiper .swiper-button {
    width: 28px;
    height: 28px;
  }
}

@media only screen and (max-width: 374px) {
  [class].swiper .swiper-button {
    bottom: 60%;
  }
}

.burger {
  --line-height: 2px;
  --buger-size: 20px;
  --move: calc(var(--buger-size) / 3);
  --move-decrement: calc(var(--move) * -1);
  --transition-time: .5s;
  --lines-color: var(--color-black);
  width: calc(var(--buger-size)  + 5px);
  height: var(--buger-size);
  transition: opacity var(--transition-time) ease;
  background-color: #0000;
  display: none;
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
}

.burger span {
  z-index: -1;
  width: 100%;
  height: var(--line-height);
  background-color: var(--lines-color);
  transition: transform var(--transition-time) ease, background-color var(--transition-time) ease .3s;
  display: block;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}

.burger span:before, .burger span:after {
  content: "";
  width: 100%;
  height: var(--line-height);
  background-color: var(--lines-color);
  animation-duration: .4s;
  animation-timing-function: cubic-bezier(.645, .045, .355, 1);
  animation-fill-mode: forwards;
  position: absolute;
  left: 0;
}

.burger span:before {
  transform: translateY(var(--move-decrement)) rotate(0);
}

.burger span:after {
  transform: translateY(var(--move)) rotate(0);
}

.burger:hover {
  opacity: 1;
}

@media only screen and (max-width: 1180px) {
  .burger {
    display: block;
  }
}

.closed.opened span {
  background-color: var(--lines-color);
  transition: background-color .2s;
}

.closed.opened span:before {
  animation-name: toplineburger;
}

.closed.opened span:after {
  animation-name: bottomlineburger;
}

.opened .nav__inner {
  transform: translateX(0);
}

.opened span {
  background-color: #0000;
  transition: background-color .2s;
}

.opened span:before, .opened span:after {
  animation-duration: .2s;
  animation-timing-function: cubic-bezier(.645, .045, .355, 1);
  animation-fill-mode: forwards;
}

.opened span:before {
  animation-name: toplinecross;
}

.opened span:after {
  animation-name: bottomlinecross;
}

.hero__content {
  flex-direction: column;
  align-items: center;
  gap: clamp(20px, 3vw, 40px);
  margin-bottom: clamp(30px, 4.5vw, 60px);
